@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&family=Lato:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body, .App, #root{
    height: 100%;
    padding: 0px;
    overflow: hidden;
    margin: 0;
    background: #CFCFCF;
}


#outerContainer {
    height: calc(100vh - 40px);
    /* height: 100%; */
    overflow: hidden;
    margin: 20px;
    display: flex;
    
    box-shadow: -6px -6px 8px #F0F0F0, 6px 6px 8px rgba(121, 123, 129, 0.5), 
    inset 4px 4px 15px rgba(14, 17, 16, 0.5), inset -4px -4px 15px rgba(94, 94, 94, 0.5);
    border-radius: 20px;
    background: #252525;
}
#dragPalette {
    width: 100%;
    touch-action: none;
    /* display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    align-content: stretch; */

    overflow: hidden;
    /* border-radius: 7px; */
    touch-action: none;
}

.dragItem {
    touch-action: none;
    /* position: absolute; */
    /* top: calc(0% + 50px);
    left: calc(0% + 50px); */
    width: 20px;
    height: 20px;
    /* background-color: #f5e663; */
    border: 3px solid #7D7D7D;
    border-radius: 50%;
    touch-action: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: border-width 0.2s, margin 0.2s;
}



@media (hover: hover) and (pointer: fine) {
    .dragItem:hover {
        cursor: pointer;
        border-width: 8px;
        margin: -5px;
    }

    .dragItem:active {
        background-color: rgba(168, 218, 220, 1.00);
    }
}

@media (hover: none) and (pointer: coarse) {
    .dragItem:active, .dragItem:focus{
        /* cursor: pointer; */
        border-width: 8px;
        margin: -5px;
        background-color: rgba(168, 218, 220, 1.00);
    }
    .dragItem.active{
        /* cursor: pointer; */
        border-width: 8px;
        margin: -5px;
        background-color: rgba(168, 218, 220, 1.00);
    }
}
#point-manager {
    position: absolute;
    top: calc(0% + 50px);
    right: calc(0% + 50px);
    /* width: 150px; */

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    align-content: stretch;

    overflow: hidden;
    /* border-radius: 7px; */
    touch-action: none;
}

#point-manager button {
    margin: 10px;
    height: 40px;
    width: 40px;

    font-size: 20px;
    font-family: 'Inter', sans-serif;
    font-weight: 900;
    color: rgb(211, 211, 211);

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    
    background: #252525;
    /* Neumorphic Up */

    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.6), -2px -2px 8px rgba(133, 129, 129, 0.5), inset -3px -3px 4px rgba(19, 20, 20, 0.5), inset 2px 2px 4px rgba(106, 107, 109, 0.5);
    /* border-radius: 24px; */

    /* background: none; */
    /* border: 3px solid rgb(233, 233, 233); */
    border: none;
    border-radius: 6px;
    padding: 0 10px 0 10px;
    /* transition: background-color 0.3s, color 0.3s; */
    transition: box-shadow 0.3s;
}

@media (hover: hover) and (pointer: fine) {
    #point-manager button:hover {
        /* background-color: rgb(211, 211, 211);
        color: #252525; */
        box-shadow: 2px 2px 8px rgba(11, 11, 11, 0.6), -2px -2px 8px rgba(101, 101, 101, 0.5), inset 6px 6px 10px rgba(28, 29, 29, 0.5), inset -5px -5px 10px rgba(45, 45, 47, 0.5);
    }
}

@media (hover: none) and (pointer: coarse) {
    #point-manager button:active {
        /* background-color: rgb(211, 211, 211);
        color: #252525; */
        box-shadow: 2px 2px 8px rgba(11, 11, 11, 0.6), -2px -2px 8px rgba(101, 101, 101, 0.5), inset 6px 6px 10px rgba(28, 29, 29, 0.5), inset -5px -5px 10px rgba(45, 45, 47, 0.5);
    }
}

#point-manager .plus:before {
    content: '\FF0B';
}

#point-manager .minus::before {
    content: '\2212';
}
